import React, { useState, useEffect } from 'react';
import mapboxgl from 'mapbox-gl'
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
import bbox from '@turf/bbox'
import locations from './drop_off_locs.json'
import district from './district.json'
import cities from './cities.json'
import centroids from './city_centroid.json'

const Location = ({ feature }) => {
  return (
    `<section class="mb-2" style="background: #0460BA;">
      <h3 class="text-lg font-extrabold p-2 text-white">${feature.properties["Location Name"]} - ${feature.properties['Street Address']}</h3>
      <div class="px-3 py-2 bg-white bg-opacity-25 text-white">
        <p class="text-lg"><b>${feature.properties.Type}</b></p>
        <p class="text-lg">Open: <b>${feature.properties.Hours}</b></p>
        <ul class="list-disc list-inside">
          ${feature.properties['Register to Vote'] === 'X' ? `<li>Register to Vote</li>` : ``}
          ${feature.properties['Get an Absentee Ballot'] === 'X' ? `<li>Get an Absentee Ballot</li>` : ``}
          ${feature.properties['Drop Off Absentee Ballot'] === 'X' ? `<li>Drop Off Absentee Ballot</li>` : ``}
        </ul>
        <p class="mt-1 italic">${feature.properties.Notes}</p>
      </div>
    </section>`
  )
}

const DropOffMap = ({ address, setAddress, city, setCity, setFiltered }) => {

    const [theMap, setTheMap] = useState(null)
    const [theGeocoder, setTheGeocoder] = useState(null)

    useEffect(() => {
        mapboxgl.accessToken =
            "pk.eyJ1Ijoiam1jYnJvb20iLCJhIjoiY2s2c3JubGd5MGFtNDNncm0yaWNmaWFpciJ9.Jmw14y-b00Q_j0UIxFmMmw";
        var map = new mapboxgl.Map({
            container: "map",
            style: "mapbox://styles/jmcbroom/ck7va71n90bf51jmj0r3n2a7d",
            center: [-83.03777, 42.376],
            zoom: 9
        });

        const geocoder = new MapboxGeocoder({
            accessToken: mapboxgl.accessToken,
            mapboxgl: mapboxgl,
            countries: 'US',
            types: 'address',
            bbox: [-83.430038, 42.179439, -82.961926, 42.444748],
            flyTo: false,
            placeholder: `Enter your address`
        })

        map.addControl(geocoder, 'top-left')

        geocoder.on('result', e => {
            setCity(null)
            setAddress(e.result)
            console.log(e.result)
            let [addr, addrCity, ...rest] = e.result.place_name.split(",")
            let filtered = locations.features.filter(l => l.properties.City === addrCity.trim())
            console.log(addrCity)
            setFiltered(filtered)
            setCity(addrCity.trim())
            map.setFilter("cities-fill", ['==', 'Label2', addrCity])
            map.getSource("locations").setData({ "type": "FeatureCollection", features: filtered })
            let fc = { type: "FeatureCollection", features: filtered }
            fc.features.push(e.result)
            map.fitBounds(bbox(fc), { padding: 50, maxZoom: 15 })
            map.setLayoutProperty('locations-label', 'visibility', 'visible')
            map.setLayoutProperty('locations-icon', 'visibility', 'visible')
            map.setLayoutProperty('locations-circle', 'visibility', 'visible')
        })

        map.on("load", e => {

            map.fitBounds(bbox(district), { padding: 25 })
            setTheMap(map)
            setTheGeocoder(geocoder)

            map.addSource("district", {
                type: 'geojson',
                data: district
            })

            map.addSource("cities", {
              type: 'geojson',
              data: cities
            })

            map.addSource("city-points", {
              type: 'geojson',
              data: centroids
            })

            map.addLayer({
                id: "cities-line",
                source: "cities",
                type: 'line',
                paint: {
                    "line-color": 'rgba(0,0,0,0.25)'
                }
            })

            map.addLayer({
                id: "cities-fill",
                source: "cities",
                filter: ["==", ["get", "Label2"], ""],
                type: 'fill',
                paint: {
                    "fill-color": 'rgba(100,0,0,0.05)'
                }
            })

            map.addLayer({
              id: "cities-label",
              type: 'symbol',
              source: 'city-points',
              paint: {
                "text-halo-color": "white",
                "text-halo-width": 1,
                "text-color": "rgba(0,0,0,0.75)"
              },
              layout: {
                  visibility: 'visible',
                  "text-field": ["get", "Label2"],
                  "text-font": ["Inter Regular"],
                  "text-size": {
                      stops: [[11, 12], [15, 20]]
                  },
                  "text-max-width": 2,
              }
          })

            map.addLayer({
                id: "district-fill",
                source: "district",
                type: 'fill',
                paint: {
                    "fill-color": 'rgba(0,0,0,0.05)'
                }
            }, 'road-label')


            map.addSource("locations", {
                type: "geojson",
                data: {type: "FeatureCollection", features: []}
            })

            map.addLayer({
                id: "locations-circle",
                type: 'circle',
                source: "locations",
                layout: {
                  visibility: "visible"
                },
                paint: {
                    "circle-radius": {
                        stops: [[9, 10], [19, 10]]
                    },
                    "circle-color": 'rgba(255,255,255,0.75)',
                    "circle-stroke-color": 'rgba(0,0,0,0.9)',
                    "circle-stroke-width": 2
                }
            })
            map.addLayer({
                id: "locations-icon",
                type: 'symbol',
                source: "locations",
                layout: {
                    "icon-image": 'town-hall-11',
                    "icon-allow-overlap": true,
                    visibility: "visible"
                }
            })

            map.addLayer({
                id: "locations-label",
                type: 'symbol',
                source: 'locations',
                layout: {
                    visibility: 'none',
                    "text-field": ["get", "Location Name"],
                    "text-font": ["Inter Regular"],
                    "text-offset": {
                        stops: [[10, [0, 2.75]], [15, [0, 1.75]]]
                    },
                    "text-letter-spacing": -0.02,
                    "text-size": {
                        stops: [[11, 10], [15, 20]]
                    }
                }
            })

            map.on('click', 'locations-icon', e => {
              let features = map.queryRenderedFeatures(e.point, {layers: ['locations-icon']})
              console.log(features[0])
              let popup = new mapboxgl.Popup()
                .setLngLat(e.lngLat)
                .setMaxWidth('320px')
                .setHTML(Location({feature: features[0]}))
                .addTo(map)
            })
        });
    }, []);

    useEffect(() => {
        if (theMap && theGeocoder && address === null) {
            theGeocoder.clear()
            theMap.getSource("locations").setData(locations)
            theMap.setLayoutProperty('locations-label', 'visibility', 'none')
            theMap.setLayoutProperty('locations-icon', 'visibility', 'none')
            theMap.setLayoutProperty('locations-circle', 'visibility', 'none')

            theMap.fitBounds(bbox(district), { padding: 50 })
        }
    }, [address, theMap, theGeocoder])

    useEffect(() => {
      if (theMap && city && theMap.isSourceLoaded("locations")) {
        let filtered = locations.features.filter(l => l.properties.City === city.trim())
        setFiltered(filtered)
        theMap.getSource("locations").setData({ "type": "FeatureCollection", features: filtered })
        let fc = { type: "FeatureCollection", features: filtered }
        theMap.fitBounds(bbox(fc), { padding: 50, maxZoom: 15 })
        theMap.setLayoutProperty('locations-label', 'visibility', 'visible')
        theMap.setLayoutProperty('locations-icon', 'visibility', 'visible')
        theMap.setLayoutProperty('locations-circle', 'visibility', 'visible')
        theMap.setFilter("cities-fill", ['==', 'Label2', city])
      }
    }, [city, theMap])

    return (
        <section>
            <div id="map" style={{ width: '100vw', height: `calc(70vh - 200px)` }} />
        </section>
    )
}

export default DropOffMap