import React, { useState } from 'react';
import DropOffMap from './DropOffMap';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVoteYea } from '@fortawesome/free-solid-svg-icons'

import locations from './drop_off_locs.json'
import { Header } from 'semantic-ui-react';

const addressMessage = (address) => {
  const [addr, city, ...rest] = address.place_name.split(',')
  return <h2>Showing drop-off locations for <b>{addr}</b> in <b>{city}</b>.</h2>
}

const Location = ({ feature }) => {
  const actions = []

  if (feature.properties['Register to Vote'] === 'X') { actions.push("Register to Vote") }
  if (feature.properties['Get an Absentee Ballot'] === 'X') { actions.push("Get an Absentee Ballot") }
  if (feature.properties['Drop Off Absentee Ballot'] === 'X') { actions.push("Drop Off Absentee Ballot") }
  return (
    <section className="mb-2 border-gray-200 border-4 text-white"> 
      <h3 className="text-xl font-extrabold p-2 text-white">{feature.properties["Location Name"]} - {feature.properties['Street Address']}</h3>
      <div className="px-3 py-2 bg-white bg-opacity-25">
        <p className="text-lg">This location is a <b>{feature.properties.Type}</b></p>
        <p className="text-lg">This location is open: <b>{feature.properties.Hours}</b></p>
        <ul className="list-disc list-inside p-2">
          {actions.map(a => <li className="my-2">{a}</li>)}
        </ul>
        <p className="mt-1 italic">{feature.properties.Notes}</p>
      </div>
    </section>
  )
}

function App() {

  const [address, setAddress] = useState(null)
  const [filtered, setFiltered] = useState([])

  let cities = Array.from(new Set(locations.features.map(l => l.properties.City))).sort()
  const [city, setCity] = useState(null)


  return (
    <div style={{background: "#0460BA"}}>
      <div>

      <header className="p-3 text-white">
        <div className="flex items-center align-middle">

        <FontAwesomeIcon icon={faVoteYea} size="lg" className="mr-2"/>
        <h1 className="text-2xl font-extrabold text-white">

          Where can you vote early before the August 4th election in MI-13?
        </h1>
        </div>
        {/* <p className="text-2xl">It depends on where you're <span className="font-semibold">registered to vote.</span></p> */}
        <p className="text-xl mt-2">
          <span className="font-semibold">Choose your city</span> or <span className="font-semibold">enter your address</span> on the map to see early voting locations.
        </p>
      </header>

      <section className="flex align-middle items-center p-3 text-white bg-white bg-opacity-25">
        <p className="text-xl pr-4">I'm <span className="font-semibold">registered to vote</span> in the city of:</p>
        <select id="city-select"
          className="block appearance-none w-50 text-xl font-bold border border-gray-200 text-gray-600 py-3 px-4 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
          value={city}
          onChange={(e) => setCity(e.target.value)}
          >
          {cities.map(c => (
            <option>{c}</option>
            ))}
        </select>
      </section>

      <section className="p-3 text-white bg-white bg-opacity-25">
        <p className="text-xl pr-4">I'm <span className="font-semibold">registered to vote</span> at this address:</p>
      </section>
            </div>


      <DropOffMap setAddress={setAddress} address={address} city={city} setCity={setCity} setFiltered={setFiltered}>

      </DropOffMap>
      {filtered.length > 0 &&
        <section className="p-3 overflow-y-auto" style={{height: '30vh'}}>
          <h2 className="text-xl mb-2 text-white font-semibold">
            {filtered.length === 1 ? `Drop off your absentee ballot early at:` : `There are ${filtered.length} locations you can visit to drop off your ballot:`}
          </h2>
          {filtered.map(f => (
            <Location feature={f} />
          ))}

        </section>}
      <hr />
    </div>
  );
}

export default App;
